/** Media Queries **/
body {
  background: #e1e1e1;
}

.Buttons-start {
  display: flex;
  grid-gap: 0.5rem;
}

.login-insert {
  width: 30rem;
  margin: 5rem auto;
}
.login-insert .info h3 {
  font-size: 3rem;
  font-weight: 500;
  font-family: "Aclonica", sans-serif;
}

.home-insert {
  width: 80%;
  margin: 2rem auto;
}
.home-insert .info h3 {
  font-size: 3.5rem;
  font-family: "Merienda", cursive;
  font-weight: 600;
}
.home-insert .list-links {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: stretch;
}
.home-insert .list-links .link-card {
  width: 100%;
}
.home-insert .list-links .link-card .my-card {
  margin: 16px;
  width: 100%;
  height: -webkit-fill-available;
}
.home-insert .list-links .link-card .btn-card {
  margin: 0;
  padding: 0;
  width: 100%;
}
.home-insert .list-links .link-card .btn-card .text-info {
  width: 100%;
}